<template>
  <section class="list">
  		<h3 v-if="title">{{title}}</h3>
  		<ul>
  			<li v-for="(item,key) in datalist" :key="key">
  				<router-link :to="item.linkurl">{{item.name}}</router-link>
  			</li>
  		</ul>
  </section>
</template>

