	      	linkurl:"/final/coursedetails"
	      },
	      {
	      	name:"",
	      	linkurl:"/final/coursedetails"
	      },
	      {
	      	name:"",
	      	linkurl:"/final/coursedetails"
	      },
	      {
	      	name:"",
	      	linkurl:"/final/coursedetails"
	      }
      ],
      actlist:[
      	{
      		name:"",
      		linkurl:"/activity/newactivity"
      	},
      	{
      		name:"",
      		linkurl:"/activity/newactivity"
      	},
      	{
      		name:"",
      		linkurl:"/activity/newactivity"
      	}
     	],
     	charDataA:[
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		},
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		},
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		},
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		}
      	],
      	charDataB:[
