						name:"",
						linkurl:"/final/coursedetails"
      		},
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		},
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		},
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		},
      		{
						name:"",
						linkurl:"/final/coursedetails"
      		}         		
      	]
    }
  },
  methods:{
    
  },
  mounted(){
  	let datali=this.datali;
    let _this=this;
    if(datali=="acvData"){
    	_this.datalist=_this.acvData;
    }
    else if(datali=="actlist"){
    	_this.datalist=_this.actlist;
    }
    else if(datali=="charDataA"){
    	_this.datalist=_this.charDataA;
    }
    else if(datali=="charDataB"){
    	_this.datalist=_this.charDataB;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";

.list{
	ul{
		display:flex;
		width:rem(576px);
		margin:0 auto rem(30px);
		flex-wrap: wrap;
		li{
			width:rem(574px);
			display: flex;
			justify-content: center;
			align-items: center;
			height:rem(86px) ;
			border: 1px #d4d4d4  solid;
			border-radius:rem(5px);
			margin-bottom:rem(4px);
