<template>
	<section>
		<h3></h3>
	  <section class="detail-top"><img :src="imgurl" alt="" /></section>
	  <section class="paragraph">
	  	<p v-for="(item,key) in pData" :key="key">{{item.content}}</p>
	  </section>
	</section>
</template>

<script>
export default {
  name: 'cdetails',
  props:{
  	imgurl:String,
  	url:{
  		type:String,
  		default:""
  	}
  },
  data () {
    return {
      pData:[]
    }
  },
  mounted(){
    let _this=this;
    _this.$http({
    	method:"get",
    	url:this.url,
    }).then(function(res){
    	_this.pData=res.data;  
    },function(err){
    	console.log("error")
    });
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/css/px2rem.scss";
h3{
	background: url(../../assets/images/acvlist-bg.png) no-repeat;
	@include font-dpr(30px);
	background-size: rem(640px) rem(71px);
	line-height:rem(71px);
	padding-left:rem(30px);
}
.detail-top{
	width: rem(583px);
	height: rem(235px);
	background-size: rem(583px) rem(235px);
	margin: rem(30px) auto;
	img{
		display:block;
		width: 100%;
		height: 100%;
	}
}
.paragraph{
	width: rem(583px);
	margin: 0 auto;
	padding-bottom: rem(30px);
	p{
		text-indent: 2em;
		@include font-dpr(24px);
		line-height: rem(42px);
		margin: rem(20px) 0 rem(20px) 0;
	}
}
</style>



