<template>
  <section>
  	<section class="order">
  		<h3><i></i></h3>
  		<p></p>
  		<form class="datum">
  			<label><input type="text" /></label>
  			<label class="age">
	  			<span>
	  				<select name="">
	  					<option value=""></option>
	  				</select>
	  			</span>	  			
	  			<span>
	  				<select name="">
	  					<option value=""></option>
	  				</select>
	  			</span>
	  			<span>
	  				<select name="">
	  					<option value=""></option>
	  				</select>
	  			</span>
  			</label>
  			<label><input type="text" placeholder=""></label>
  			<label>
	  			<select name="">
	  				<option value=""></option>
	  			</select>
  			</label>
  			<label>
	  			<select name="">
	  				<option value="">	</option>
	  			</select>
  			</label>
  			<label>
	  			<select name="">
	  				<option value=""></option>
	  			</select>
  			</label>
  			<input class="submit" type="submit" value=""/>
  		</form>
  	</section>	  
  </section>
</template>

<script>
	
export default {
  name: 'order',
  data () {
    return {
      
    }
  },
  mounted(){
    
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../assets/css/px2rem.scss";


.order{
	width:rem(594px);
	margin:rem(24px) auto;
	background:#fff;
	border:1px #d0d0d0 solid;
	border-bottom: 7px #443c3c solid;
	border-radius: 0 0 rem(10px) rem(10px);
	h3{
		position:relative;
		@include font-dpr(36px);
		background:#443c3c;
		color:white;
		line-height:rem(105px);
		padding-left:rem(32px);
		width:rem(594px);
		box-sizing:border-box;
		border-radius:rem(5px) rem(5px) 0 0;
		i{
			position: absolute;
			width: rem(210px);
			height: rem(201px);
			background:url(../assets/images/order_topbg.png) no-repeat;
			background-size: rem(210px) rem(201px);
			right: rem(-14px);
			top: rem(-14px);
		}
	}
	p{
		background: #f0f0f0;
		margin: rem(14px);
		@include font-dpr(18px);
		line-height:rem(34px);
		padding:rem(22px) rem(32px);
		border-radius:rem(5px);
	}
}
.datum{
	margin: rem(23px) auto;
	width:rem(495px);
	label{
		display:block;
		margin-bottom: rem(14px);
		width: rem(480px);
		overflow: hidden;
		white-space: nowrap;
		@include font-dpr(20px);
		input{			
			width: rem(360px);
			height: rem(42px);
			border:1px #e3e3e3 solid;
			padding-left: rem(10px);
			color: #c5c5c5;
			margin-left: rem(6px);
		}	
		select{
			width: rem(460px);
			padding-left: rem(6px);
			border:1px #e3e3e3 solid;
			height: rem(42px);
			color: #c5c5c5;
			background: url(../assets/images/map_arrow.png) rem(330px) 0 no-repeat;
			background-size: rem(45px) rem(42px);
		}
		
	}
	.age{
		display: flex;
		&>:nth-child(1){
			width: rem(147px);
			margin-left: rem(6px);
			select{
				background: url(../assets/images/map_arrow.png) rem(104px) 0 no-repeat;
				background-size: rem(45px) rem(42px);
			}
		}
		span{
			overflow: hidden;
			width: rem(100px);
			margin-left: rem(13px);
			select{
				background: url(../assets/images/map_arrow.png) rem(55px) 0 no-repeat;
				background-size: rem(45px) rem(42px);
			}
		}		
	}
	
	.submit{
		margin-top: rem(113px);
		margin-bottom: rem(39px);
		width: rem(488px);
		height: rem(63px);
		color: white;
		background: #f52c22;
		border: 0;
		border-radius: rem(5px);
	}
}


</style>



