<template>
  <div id="app" :style="{left:+appMove+'px'}">
    <headerA class="header"></headerA>    
    <transition :enter-active-class="ani_enter" :leave-active-class="ani_leave">
    	<router-view class="main"></router-view>
    </transition>
    <footerA class="footer"></footerA>    
  </div>
</template>

<script>
import Bus from "@/assets/js/bus.js"
import flexible from "./assets/js/fiexible.js";
import headerA from '@/components/common/header';
import footerA from '@/components/common/footer';
flexible();

export default {
  name: 'app',
  data(){
    return {
			appMove:0,
			navMove:0,
    }
  },
  components:{
  	headerA,
  	footerA
  },
  beforeUpdate(){
  	let path=this.$route.path;
  	var arr=path.match(/\//g);
  	this.$store.commit("ani_transition",{
  		class:arr.length
  	}); 
  },
  computed:{
  	ani_leave(){
  		return this.$store.state.ani.ani_leave;
  	},
  	ani_enter(){
  		return this.$store.state.ani.ani_enter;
  	}	
  },
  methods:{
   
  },
  mounted(){
  	let _this=this;
  	Bus.$on("move",function(data){ 		
  		if(_this.appMove==0){
  			_this.appMove=data;  			
  		}
  		else{
  			_this.appMove=0;    		
  		}
  	})  	
  }
}
</script>

<style lang="scss">
@import "./assets/css/font-awesome.min.css";
@import "./assets/css/animate.min.css";
@import "./assets/css/px2rem.scss";

html,body{height: 100%;background:#f1f2f2;}
.animated{position:fixed;  z-index:999;}	
#app {
  transition: left 0.2s;
  position: relative;
	box-sizing: border-box;
	padding: rem(80px) 0 rem(170px);
	min-height: 100%;
}
.header{position: absolute; left: 0; top: 0;z-index: 100000;}
.main{width: rem(640px);background:#f1f2f2;min-height: 100%;}
.footer{position: absolute; left: 0; bottom: 0; width: 100%;z-index: 99;}


$color:#f00;
a{
  color:$color;
}
a:hover{
	text-decoration: none;
} 
.leave{
  animation-name:leave;
}
@keyframes leave{
  0%{
    transform:translateX(0);
  }
  100%{
    transform:translateX(-20%);
  }
}
.enter{
  animation-name:enter;
  z-index: 99999;
}
.fadeIn{
	z-index:99999;
}

@keyframes enter{
  0%{
    transform:translateX(100%);
  }
  100%{
    transform:translateX(0);
  }
}

</style>





