import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);//vuex
import ani from "./moudles/ani";
import refreshMore from "./moudles/refreshMore";
export default new Vuex.Store({
	state:{//
	},
	getters:{//
	},
	mutations:{//
	},
	actions:{//
	},
	modules:{//
		ani,
		refreshMore,
		app:{//app
			state:{//
				count:0
			},
			getters:{//
			},
			mutations:{//;;commitcommit
			
			},
			actions:{//;;dispatch
				
			},
			modules:{//
			}
		},
		list:{//app
			state:{//
				count:10
			},
			getters:{//

			},
			mutations:{//;;commit
				add(state,payload){//statepayloadcommit
					state.count++;
					console.log(payload);
				},
				reduce(state,payload){
					state.count--;
					console.log(payload);
				}
			},
			actions:{//;;dispatch
				add(context,payload){//
					console.log(context);
					context.commit("add",payload);
				},
				reduce(context,payload){//
					console.log(context);
					context.commit("reduce",payload);
				}
			},
			modules:{//

			}
		}
	}
});


