<template>
  <section> 	
		  <section>	  	
			  	<h3 v-if="title">{{title}}</h3>
			  	<ul class="actlist">
			  		<li v-for="(item,key) in actdata">	 
			  			<img v-if="vip" class="viplogo" src="../../assets/images/viplogo.png" alt="" />
			  			<img :src="item.imgurl" alt="" />
			  			<span>:{{item.time}}</span>
			  			<span>:{{item.obj}}</span>
			  			<span>:{{item.address}}</span>
			  			<router-link :to="'/activity/newactivity/newactdetail'"></router-link>
			  		</li>
			  	</ul>		  	
		  </section>  
  </section>
</template>

<script>

export default {
  name: 'acticom',
  props:{
  	title:String,
  	url:{
  		type:String,
  		default:""
  	},
  	vip:String
  },
  data () {
    return {
      actdata:[],
    }
  },
  mounted(){
    let _this=this;
    _this.$http({
    	method:"get",
    	url:this.url,
    }).then(function(res){
    	_this.actdata=res.data;
    },function(err){
    	console.log("error")
    });
  },
  components:{
  	
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/css/px2rem.scss";
h3{
	@include font-dpr(30px);
	background: url(../../assets/images/acvlist-bg.png) no-repeat;
	background-size: rem(640px) rem(71px);
	line-height:rem(71px);
	padding-left:rem(30px);
	color:#039ddb;
}
.actlist{
	li{
		position: relative;
		width: rem(590px);
		border-radius: rem(5px);
		background: #fff;
		margin: rem(20px) auto rem(69px);
		box-sizing: border-box;
		padding: rem(10px) rem(8px) rem(12px);
		img{
			width:rem(576px);
			height: rem(278px);
		}
		.viplogo{
			width: rem(65px);
			height: rem(65px);
			position: absolute;
			left: rem(1px);
			top: rem(1px);
		}
		span{
			display: block;
			padding-left: rem(20px);
			line-height: rem(38px);
			@include font-dpr(24px);
			color: #453d3d;
		}
		a{
			display: block;
			width: rem(565px);
			height: rem(72px);
			text-align: center;
			line-height: rem(72px);
			margin: rem(10px) auto;
			background: #22c3f8;
			color: white;
			border:1px #0895d1 solid;
			border-radius: rem(5px);
			box-shadow: 0 0 3px #e5e5e5;
			@include font-dpr(30px);
		}
	}
}

</style>



