import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import brand from '@/components/brand'
import courseintr from '@/components/courseintr'
import coursedetails from '@/components/coursedetails'
import activity from '@/components/activity'
import newactivity from '@/components/newactivity'
import newactdetail from '@/components/newactdetail'
import mbanews from '@/components/mbanews'
import map from '@/components/map'
import order from '@/components/order'
import contact from '@/components/contact'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
     {
      path: '/brand',
      name: 'brand',
      component: brand
    },
    {
      path: '/courseintr',
      name: 'courseintr',
      component: courseintr
    },
    {
      path: '/final/coursedetails',
      name: 'coursedetails',
      component: coursedetails
    },
    {
      path: '/activity',
      name: 'activity',
      component: activity
    },
    {
    	path: '/activity/newactivity',
      name: 'newactivity',
      component: newactivity
    },
    {
    	path: '/activity/newactivity/newactdetail',
      name: 'newactdetail',
      component: newactdetail
    },
    {
    	path: '/mbanews',
      name: 'mbanews',
      component: mbanews
    },
    {
    	path: '/map',
      name: 'map',
      component: map
    },
    {
    	path: '/order',
      name: 'order',
      component: order
    },
    {
    	path: '/contact',
      name: 'contact',
      component: contact
    }
    
    ]
})



