<template>
  <section>
  	<textarea class="board" v-model="textarea" placeholder="..."></textarea>
  	<button class="btn" @touchstart="submit"></button>
  	<ul class="Leavingmsg">
  		<li v-for="(item,key) in speak" :key="key">
  			<section class="name">
  				<span>{{item.name}}</span> 
  				<i class="time">{{nowtime}}</i>
  			</section>						
  			<p>{{item.text}}</p>
  		</li>
  	</ul>
  	<section class="pop" v-if="popstate">
  		
  		<input type="text" placeholder=".." v-model="dumname"/>
  		<button @touchstart="surename"></button>
  	</section>
  </section>
</template>

<script>
import io from 'socket.io-client';


export default {
  name: 'contact',
  data () {
    return {    
      ws:null,
      speak:[],
      name:null,
      textarea:'',
      dumname:'',
     	nowtime:null,
     	popstate:true
    }
  },
  mounted(){
  	let _this=this
    _this.ws=io("http://localhost:8088");
   	_this.ws.on("connect",function(){
   		let name,text;
  		_this.ws.on("allchat",function(data){
				_this.speak.unshift(data);
				
			})
    }) 	
   	//_this.name=window.prompt(""); 	
  },
  methods:{
  	submit(){
  		let _this=this;		
   		let sentence=_this.textarea;
   		let name,text;
   		_this.ws.emit("chat",{//chat
					name:_this.name,
					text:sentence
				});
			let nowtime=new Date();
			_this.nowtime=nowtime.getMonth()+"-"+nowtime.getDate()+" ";
			_this.nowtime+=nowtime.getHours()+":"+nowtime.getMinutes();
			//console.log(_this.nowtime)
  	},
  	surename(){
  		let _this=this;
  		_this.name=_this.dumname;
  		if(_this.name!=""){
  			_this.popstate=false;
  		} 		
  	}
  	
  },
  computed:{
    
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../assets/css/px2rem.scss";

.board{
	box-sizing: border-box;
	width: 100%;
	border: 0;
	padding-left: rem(10px);
	line-height: rem(40px);
}
.Leavingmsg{
	margin-top: rem(30px);
	width: 100%;
	box-sizing: border-box;
	li{
		background: #fff;
		margin-bottom: rem(20px);				
		.name{
			padding:rem(10px) 0 0 rem(60px);
			span{
				display: block;
				@include font-dpr(24px)
			}
			i{
				font-style: normal;
				@include font-dpr(18px)
			}
		}
		p{
			padding: rem(30px) rem(70px) rem(40px);
			text-align: center;
		}
	}
}
.btn{
	padding: rem(5px) rem(8px);
	border: 0;
	border-radius: rem(5px);
	@include font-dpr(18px);
}

.pop{
	position: fixed;
	z-index: 999;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-flow: wrap;
	background: rgba(0,0,0,0.5);
	input{
		border: 0;
		height: rem(50px);
		width: rem(270px);
		border-radius: rem(10px);
		padding-left: rem(10px);
	}
	button{
		display: block;
		margin-left: rem(20px);
		padding: rem(5px) rem(8px);
		border: 0;
		border-radius: rem(5px);
		@include font-dpr(18px);
	}
}
</style>



