<template>
  <section class="list">
  		<ul>
  			<li v-for="(item,key) in navData" :key="key">
  				<router-link :to="item.linkurl">{{item.name}}</router-link>
  			</li>
  		</ul>
  </section>
</template>

<script>
export default {
  name: 'navlist',
  data() {
    return {
      navData:[],
    }
  },
  mounted(){
    let _this=this;
    _this.$http({
      method:"get",
      url:"./static/api/list.json",
    }).then(function(res){
      _this.navData=res.data;
    },function(err){
    	console.log("error")
    });
  },
  methods:{
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";


.list{
	ul{
		display:flex;
		justify-content: space-between;
		flex-wrap:wrap;
		width: rem(620px);
		margin: rem(14px) auto;
		&>:nth-child(1){
			background: url(../../../static/img/navlistbg1.png) no-repeat;
			background-size: rem(192px) rem(148px);
		}
		&>:nth-child(2){
			background: url(../../../static/img/navlistbg2.png) no-repeat;
			background-size: rem(192px) rem(148px);
		}
		&>:nth-child(3){
			background: url(../../../static/img/navlistbg3.png) no-repeat;
			background-size: rem(192px) rem(148px);
		}
		&>:nth-child(4){
			background: url(../../../static/img/navlistbg4.png) no-repeat;
			background-size: rem(192px) rem(148px);
		}
		&>:nth-child(5){
			background: url(../../../static/img/navlistbg5.png) no-repeat;
			background-size: rem(192px) rem(148px);
		}
		&>:nth-child(6){
			background: url(../../../static/img/navlistbg6.png) no-repeat;
			background-size: rem(192px) rem(148px);
		}
		li{
			box-sizing: border-box;
			width:rem(192px);
			height: rem(148px);
			display: flex;
			justify-content:center;			
			margin-bottom:rem(24px);
			a{
				display: block;
				width: 100%;
				height: 100%;
				padding-top: rem(117px);
				text-align: center;
				color:#fff;
				@include font-dpr(18px);
			}
		}
	}
}

</style>


