<template>
  <section>
	  <h3></h3>
	  <form>
	  	<label for="address">
		  	<select name="address" id="address">
		  		<option value="0"></option>
		  		<option value="1">1</option>
		  		<option value="2">2</option>
		  	</select>
		</label>
	  </form>
	  <p><span>ADD</span><i></i></p>
	  <p><span>TEL</span>86-755-9999999</p>
	  <section class="map"><img src="../../static/img/map.jpg"/></section>
  </section>
</template>

<script>
	
export default {
  name: 'map',
  data () {
    return {
      
    }
  },
  mounted(){
    
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../assets/css/px2rem.scss";
h3{
	@include font-dpr(30px);
	background: url(../assets/images/acvlist-bg.png) no-repeat;
	background-size: rem(640px) rem(71px);
	line-height:rem(71px);
	padding-left:rem(30px);
	color:#039ddb;
}
form{
	width: rem(540px);
	margin:rem(24px) 0 rem(24px) rem(38px);
	border: 0;
	overflow: hidden;
	label{
		display:block;	
		position: relative;	
		select{
			width:rem(640px);
			height: rem(62px);
			padding-left: rem(32px);
			color:#787878;
			background: #fff url(../assets/images/map_arrow.png) rem(475px) -1px no-repeat;
			background-size: rem(65px) rem(61px);
			}
	}	
}


.map{
	background: white;
	padding: rem(18px) 0;
	margin-top: rem(16px);
	img{
	width: rem(640px);
	height: rem(504px);
	}
}


p{
	@include font-dpr(24px);
	line-height: rem(36px);
	color:#787878;
	width:rem(580px);
	margin:0 auto;
	display:flex;
	span{
		color: #009bda;
		flex:none;
	}
	i{
		font-style: normal;
	}
}
</style>



