<template>
  <section class="slider" @touchstart="move" @touchmove="move" @touchend="move" ref="sliderObj"> 
    <ul :style="{transform:'translateX('+sliderMove.x_move+'px)',transition:sliderMove.transVal}">
    	<li v-if="(listimgData.length-1)!=-1">
    		<router-link :to="listimgData[listimgData.length-1].linkurl"><img :src="listimgData[listimgData.length-1].img"/></router-link>
    	</li>
    	<li v-for="(item,key) in listimgData" :key="key">
    		<router-link :to="item.linkurl"><img :src="item.img"/></router-link>
    	</li>
    	<li v-if="(listimgData.length-1)!=-1">
    		<router-link :to="listimgData[0].linkurl"><img :src="listimgData[0].img"/></router-link>
    	</li>
    </ul>
    <section class="slider-btn">
    	<span v-for="(item,key) in listimgData" :key="key" :class="{current:key==sliderMove.index}"></span>
    </section>
  </section>
</template>

<script>
export default {
  name: 'slider',
  data () {
    return {
      listimgData:[],
      sliderMove:{
        x_start:null,//touchstartx
        x_init:0,//x
        x_move:0,//x
        transVal:"none",//
        index:0//
      }
    }
  },
  mounted(){
    let _this=this;
    _this.$http({
      method:"get",
      url:"./static/api/slierimg.json",
    }).then(function(res){
      _this.listimgData=res.data;
    },function(err){
      console.log(err)
    });
    
    let width=_this.$refs.sliderObj.clientWidth;
    _this.sliderMove.x_init=-width;
    _this.sliderMove.x_move=-width;
  },
  methods:{
  	move(e){
  		let _this=this;
  		//console.log(e)
  		let x=e.changedTouches[0].pageX;
  		if(e.type=="touchstart"){
        _this.sliderMove.transVal="none";
        _this.sliderMove.x_move=_this.sliderMove.x_init;
        _this.sliderMove.x_start=x;
      }
      else if(e.type=="touchmove"){
        _this.sliderMove.x_move=_this.sliderMove.x_init+(x-_this.sliderMove.x_start);
        //console.log(_this.sliderMove.x_move);
      }
      else if(e.type=="touchend"){
        let distance=x-_this.sliderMove.x_start;//
        let width=_this.$refs.sliderObj.clientWidth;//
        let result=0;//
        _this.sliderMove.index=Math.abs(Math.round(_this.sliderMove.x_init/width))-1;//
        _this.sliderMove.transVal="transform 0.2s";//
        if(Math.abs(distance)>width/3){
          if(distance>0){//
            result=_this.sliderMove.x_init+width;
            _this.sliderMove.index=_this.sliderMove.index==0?_this.listimgData.length-1:_this.sliderMove.index-1;//
          }
          else{//
            result=_this.sliderMove.x_init-width;
            _this.sliderMove.index=_this.sliderMove.index==_this.listimgData.length-1?0:_this.sliderMove.index+1;//
          }
        }
        else{
          result=_this.sliderMove.x_init;
        }
        _this.sliderMove.x_move=result;
        _this.sliderMove.x_init=-(_this.sliderMove.index+1)*width;
      }
  	}
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";

.slider{	
	width: 100%;
	position: relative;
	overflow: hidden;
		ul{
			height: rem(370px);
			width:1000%;			
			display: flex;
			li{
				width:10%;	
				height: rem(370px);		
				img{
					height:rem(370px);
					
				}
				
			}
		}
		.slider-btn{
			position:absolute ;
			bottom: 0;
			height: rem(44px);
			width: 100%;
			background: rgba(255,255,255,0.5);
			display: flex;
			justify-content: center;
			align-items: center;
			span{
				width: rem(15px);
				height: rem(15px);
				border-radius: 50%;
				background: #fff;
				margin-right: rem(8px);
			}
			.current{
				background: #00f;
			}
		}
		
	}
	
</style>



