<template>
  <section class="head">
  		<section @touchstart.prevent="appMove" class="menu"><i class="fa fa-navicon"></i></section>
  		<section class="heade"></section>
  		<section class="contact" href="tel:15555555559"><i class="fa fa-phone"></i></section>
  		<section class="nav" ref="nav" :style="{left:navMove+'px'}">
  			<router-link v-for="(item,key) in navData" :to="item.url" :key="key"><i class="fa" :class="item.className"></i>{{item.name}}</router-link>
  		</section>
  		<section class='mask' v-if="navMove==0" @touchstart.prevent="appMove"></section>
  </section>
</template>

<script>
import Bus from "@/assets/js/bus.js";
export default {
  name: 'header',
  data() {
    return {
      headname: '',
      navData:[
      {
      	url:"/",
      	name:"",
      	className:"fa-home"
      },
      {
      	url:"/brand",
      	name:"",
      	className:"fa-cube"
      },
      {
      	url:"/courseintr",
      	name:"",
      	className:"fa-book"
      },
      {
      	url:"/activity",
      	name:"",
      	className:"fa-home"
      },
      {
      	url:"/mbanews",
      	name:"MBA",
      	className:"fa-home"
      },
      {
      	url:"/mbanews",
      	name:"",
      	className:"fa-home"
      },
      {
      	url:"/order",
      	name:"",
      	className:"fa-home"
      },
      {
      	url:"/contact",
      	name:"",
      	className:"fa-home"
      }
      ],
      navMove:null
	  }
  },
  methods:{
    appMove(){
    	let _this=this;
    	let navNode=this.$refs.nav;
    	let width=navNode.clientWidth; 	
    	Bus.$emit("move",width);  
    	if(_this.navMove==0){
    		_this.navMove=-width;
    	}
    	else{
    		_this.navMove=0;
    	}
    }
    
  },
  mounted(){
  	let _this=this;
		let navNode=this.$refs.nav;
		let width=navNode.clientWidth; 
  	_this.navMove=-width;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/font-awesome.min.css";
@import "../../assets/css/px2rem.scss";

.head{
	display: flex;
	background: url(../../assets/images/headerbg.png) no-repeat;
	background-size: rem(640px) rem(80px);
}
.menu{
	width: rem(95px);
	height: rem(80px);
	display: flex;
	justify-content: center;
	align-items: center;
	i{
		@include font-dpr(48px);
		color:#afa9a9;
	}
}
.heade{
	display: flex;
	box-sizing: border-box;
	width: rem(450px);	
	text-align: center;
	align-items: center;
}
.mask{position: fixed;height: 100%; z-index: 100000;right: 0;top: 0;width: rem(355px);}
.contact{
	width: rem(95px);
	display: flex;
	justify-content: center;
	align-items: center;
	i{
		@include font-dpr(64px);
		color:#afa9a9;
	}
}

.nav{
	transition: left 0.2s; 
	position: fixed;	
	z-index: 10000;
	display: flex;
	left:rem(-285px);
	top: 0;
	width: rem(285px);
	flex-wrap: wrap;
	background: #3c3535;
	height: 100%;
	align-content: flex-start;
	a{
		box-sizing: border-box;
		display: flex;
		align-items: center;
		color: #fff;
		@include font-dpr(30px);
		width: rem(295px);
		height: rem(99px);
		padding-left: rem(40px);
		border-top:1px #584c4c solid;
		border-bottom:1px #1e1818 solid;
		i{
			margin-right: rem(14px);
		}
	}
}

</style>


