<template>
  <section>
  	<h3 v-if="title">{{title}}</h3>
  	<ul class="titlist">
  		<li v-for="(item,key) in dataMba" :key="key">
  			<router-link :to="item.linkurl">{{item.tit}}</router-link>
  		</li>
  	</ul>
  </section>
</template>

<script>

	
export default {
  name: 'mbacom',
  props:{
  	title:String,
  	url:{
  		type:String,
  		default:""
  	}
  },
  data () {
    return {
      dataMba:[],
    }
  },
  mounted(){
    let _this=this;
    let url=this.url;
    _this.$http({
    	method:"get",
    	url:url,
    }).then(function(res){
    	_this.dataMba=res.data;  	
    },function(err){
    	console.log("error")
    });
    
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/css/px2rem.scss";
.mba-top{
	height: rem(183px);
	background: url(../../assets/images/mba-top.jpg) no-repeat;
	background-size: 100% rem(183px);
}
.titlist{
	li{
		border-top: 1px #d4d4d4 solid;
		background: #f1f2f2 url(../../assets/images/arrow.png) rem(600px) center no-repeat;
		background-size: rem(12px) rem(20px);
		a{
			display: block;
			color: #000;
			@include font-dpr(24px);
			line-height: rem(66px);
			padding-left: rem(54px);
			
		}
	}
}
h3{
	border-top: 1px #d4d4d4 solid;
	background: url(../../assets/images/mba-tit-bg.png) no-repeat;
	background-size: rem(640px) rem(71px);
	display: block;
	line-height: rem(71px);
	@include font-dpr(30px);
	padding-left: rem(54px);
}



</style>



